
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import background from "../leadspace.jpeg"
import Map from "./Map";
import { Line } from "react-chartjs-2";
import LineChart from './Chart';


const Home = () => {
    const [city, setCity] = useState('');
    const [weatherData, setWeatherData] = useState(null);
    const [error, setError] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const [forecastData, setForecastData] = useState(null);
    const [chartData,setChartData] = useState({});
    const API_KEY = '62321a27ce88d6bd77661d04aac7a4ee';

    useEffect(() => {


    })

    const fetchWeather = async () => {
        setIsLoading(true);
        let apiUrl;
        apiUrl = `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${API_KEY}`;

        const response = await axios.get(apiUrl).then((response) => {
            setWeatherData(response.data);
            setError(null)
        })
            .catch((error) => {
                setWeatherData(null);
                setError('Weather data not available. Please try again.');

            }).finally((res) => { res = setIsLoading(false) })

        const forecastApi = `https://api.openweathermap.org/data/2.5/forecast?q=${city}&appid=${API_KEY}`;

        try {
            const forecastResponse = await axios.get(forecastApi);
            setForecastData(forecastResponse.data);
        } catch (error) {
            setForecastData(null);
        } finally {
            setIsLoading(false);
        }

        
    };


    return (
        <>
            <div style={{
                display: "flex",
                alignItems: "center",
                height: '400px',
                backgroundSize: 'cover',
                backgroundPosition: 'center',
                backgroundImage: `url(${background})`,
                opacity: '0.9'
            }} >
                <div>
                    <p style={{ color: 'gold', marginLeft: '150px', fontSize: '50px' }}>Weather Details Fast</p>
                    <p style={{ color: 'mediumvioletred ', marginLeft: '150px', fontSize: '50px' }}>Get the Forecast You Deserve</p>
                </div>
            </div>

            <div style={{ margin: '50px', justifyContent:'center',textAlign:"center",alignItems:'center'}}>
                <div>
                    <input
                        type="text"
                        placeholder="Enter city name"
                        value={city}
                        onChange={(e) => setCity(e.target.value)
                        }
                        style={{
                            width: '300px', height: '40px', borderRadius: '5px',
                            padding: '25px', fontSize: '20px'
                        }}
                    />
                    <button onClick={fetchWeather} disabled={isLoading} className='btn btn-success' style={{ marginLeft: '30px', height: '52px' }}>
                        {isLoading ? 'Fetching Weather...' : 'Get Weather'}
                    </button>
                </div>

                {error && <p>{error}</p>}
                
                {weatherData && (
                    <div style={{ color: 'black', marginTop: "30px" }}>
                        <div>
                        <div>
                            <h2 style={{ fontSize: '30px' }}>
                                {weatherData.name}, <span>{weatherData.sys.country}</span>
                            </h2>
                        </div>
                        <div style={{ fontSize: '40px', fontWeight: "bold" }}>
                            <img
                                className=""
                                src={`https://openweathermap.org/img/wn/${weatherData.weather[0].icon}@2x.png`}
                                alt={weatherData.weather[0].description}
                            />
                            {Math.round(weatherData.main.temp) - 273}
                            <sup style={{ fontSize: '30px', verticalAlign: 'super' }}>°C</sup>
                        </div>
                        <div style={{ fontSize: '30px' }}>
                            <p>{weatherData.weather[0].description.toUpperCase()}</p>
                            <p>Wind Speed: {weatherData.wind.speed}m/s</p>
                        </div>
                        </div>
                        {
                            forecastData && (
                                <div style={{ marginTop: '30px', background: '#f7f7f7', padding: '20px', borderRadius: '10px' }}>
                                    <h3 style={{ margin: '30px' }}>Today's Forecast</h3>
                                    <ul style={{ listStyle: 'none',marginBottom:'40px' }}>
                                        {forecastData.list.slice(0, 6).map((data) => (
                                            <li key={data.dt} style={{ fontSize: '18px', marginBottom: '8px', display: 'inline-block', margin: '0 10px' }}>
                                                <div style={{ fontSize: '25px', fontWeight: 'bold', padding: '10px', height: '120px', width: '150px', background: 'skyblue', border: '2px solid black', borderRadius: '10px', textAlign: 'center' }}>
                                                    <p> {new Date(data.dt * 1000).toLocaleTimeString()}</p>
                                                    <p>{Math.round(data.main.temp) - 273}°C</p>
                                                </div>
                                            </li>
                                        ))}
                                    </ul>
                                </div>
                            )
                        }

                      
                <div style={{ color: 'black' }}>
                    <LineChart city={city}/>
                    <Map latitude={weatherData.coord.lat} longitude={weatherData.coord.lon} />
                </div>
                        
                    </div>
                )}
            </div>
        </>
    );  
};

export default Home;
