import React, { useEffect, useState } from "react";
import Chart from "chart.js/auto";
import { Line } from "react-chartjs-2";

const LineChart = ({city}) => {
  const [forecastData, setForecastData] = useState(null);
  
     const apiKey = "62321a27ce88d6bd77661d04aac7a4ee";

    fetch(
      `https://api.openweathermap.org/data/2.5/forecast?q=${city}&appid=${apiKey}`
    )
      .then((response) => response.json())
      .then((data) => {
        
        const labels = data.list.slice(0,10).map((entry) => entry.dt_txt);
        const temperatures = data.list.slice(0,10).map((entry) => (entry.main.temp)-273);

        setForecastData({
          labels: labels,
          datasets: [
            {
              label: "Temperature Forecast",
              backgroundColor: "rgb(255, 99, 132)",
              borderColor: "rgb(255, 99, 132)",
              data: temperatures,
            },
          ],
        });
      })
      .catch((error) => {
        console.error("Error fetching data from OpenWeather API", error);
      });
  

  return (
    <div>
      {forecastData ? (
        <Line data={forecastData} style={{height:'500px'}} />
      ) : (
        <p>Loading forecast data...</p>
      )}
    </div>
  );
};

export default LineChart;
